.address-page {
  width: 100%;
  background: #ffffff;
  color: #000000;
  display: flex;
  justify-content: center;
  padding: 80px 20px 120px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.address-inner {
  max-width: 800px;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  color: #000;
}

.address-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-weight: 400;
  font-size: clamp(2rem, 1rem + 2vw, 2.4rem);
  line-height: 1.2;
  margin: 0 0 16px;
  color: #000;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
}

.address-icon {
  margin-bottom: 24px;
}
.rings-mini {
  width: 42px;
  height: auto;
  stroke: #000;
  fill: none;
  display: inline-block;
}

.address-textblock {
  margin-bottom: 32px;
  color: #000;
  line-height: 1.5;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.hall-name {
  font-size: 1.1rem;
  margin: 0 0 8px;
  font-weight: 400;
}
.hall-name span {
  font-style: italic;
  font-weight: 500;
}

.hall-address {
  font-size: 1rem;
  margin: 0;
  font-weight: 400;
  color: #000;
}

/* карта в рамке */
.map-frame {
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  border: 1px solid rgba(0,0,0,0.15);
  border-radius: 12px;
  padding: 8px;
  box-shadow:
    0 20px 60px rgba(0,0,0,0.08),
    0 1px 1px rgba(0,0,0,0.4) inset;
  background: #fff;
}

.leaflet-map {
  width: 100%;
  height: 360px;
  border-radius: 8px;
  overflow: hidden;
}

/* точка-маркер наша, не битая png */
.custom-dot-marker .dot-outer {
  width: 24px;
  height: 24px;
  background: #000;
  border-radius: 50%;
  border: 3px solid #fff;
  box-shadow: 0 6px 16px rgba(0,0,0,0.4);
  position: relative;
}
.custom-dot-marker .dot-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  background: #fff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

/* блок кнопок */
.address-actions {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 14px 20px;
  margin-top: 28px;
  margin-bottom: 16px;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
}

.addr-btn {
  cursor: pointer;
  text-decoration: none;
  background: #000;
  color: #fff;
  font-size: 0.9rem;
  line-height: 1.2;
  font-weight: 400;
  padding: 12px 20px;
  border-radius: 999px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  border: 1px solid #000;
  box-shadow: 0 12px 24px rgba(0,0,0,0.18);
  transition: all 0.25s ease;
}
.addr-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 16px 28px rgba(0,0,0,0.28);
}

.addr-btn-outline {
  background: #fff;
  color: #000;
  border: 1px solid rgba(0,0,0,0.4);
  box-shadow: 0 10px 22px rgba(0,0,0,0.08);
}
.addr-btn-outline:hover {
  background: #000;
  color: #fff;
  border-color: #000;
  box-shadow: 0 16px 28px rgba(0,0,0,0.24);
}

/* подсказка для гостей */
.arrival-hint {
  font-size: 0.9rem;
  line-height: 1.4;
  font-weight: 400;
  color: rgba(0,0,0,0.7);
  margin: 8px 0 0;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
}

/* leaflet zoom кнопки немного стилизуем */
.leaflet-control-zoom {
  border: 1px solid rgba(0,0,0,0.25) !important;
  border-radius: 6px !important;
  box-shadow: 0 6px 20px rgba(0,0,0,0.15) !important;
}
.leaflet-control-zoom a {
  background: #fff !important;
  color: #000 !important;
  font-weight: 500;
  border-bottom: 1px solid rgba(0,0,0,0.15) !important;
}
.leaflet-control-zoom-in {
  border-radius: 6px 6px 0 0 !important;
}
.leaflet-control-zoom-out {
  border-radius: 0 0 6px 6px !important;
  border-bottom: 0 !important;
}

/* адаптив */
@media (max-width: 500px) {
  .address-page {
    padding: 60px 16px 80px;
  }

  .address-title {
    font-size: 2rem;
  }

  .hall-name {
    font-size: 1rem;
  }

  .hall-address {
    font-size: 0.95rem;
    line-height: 1.4;
  }

  .map-frame {
    padding: 6px;
    border-radius: 10px;
  }

  .leaflet-map {
    height: 260px;
    border-radius: 8px;
  }

  .rings-mini {
    width: 36px;
  }

  .addr-btn {
    font-size: 0.8rem;
    padding: 11px 18px;
    letter-spacing: 0.1em;
  }
}
