.invite {
  background-color: #f9f4ef;
  color: #3a3a3a;
  font-family: "Playfair Display", "Cormorant Garamond", serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 24px 16px;
  text-align: center;
}

/* Заголовок "Wedding day" */
.invite-title {
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 0.05em;
  color: #5a5a5a;
  margin-bottom: 10px;
}

/* Имена */
.invite-names {
  font-size: 2rem;
  font-weight: 400;
  font-style: italic;
  margin-bottom: 20px;
}
.invite-names span {
  font-family: "Great Vibes", cursive;
  font-size: 2.4rem;
  color: #2b2b2b;
}

/* Фото, разделённое на 3 вертикальных части */
.photo-split {
  display: flex;
  justify-content: center;
  gap: 4px;
  width: 100%;
  max-width: 350px;
  margin-bottom: 30px;
}
.slice {
  overflow: hidden;
  flex: 1;
  border-radius: 6px;
}
.slice img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: scale(1.05);
}

/* Дата */
.invite-date {
  font-size: 1.3rem;
  letter-spacing: 0.1em;
  color: #3a3a3a;
  margin-top: 12px;
  position: relative;
}
.invite-date::after {
  content: "";
  display: block;
  width: 60%;
  height: 1px;
  background: rgba(0, 0, 0, 0.2);
  margin: 10px auto 0;
}
