.gallery-section {
  background: #ffffff;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: 80px 20px 120px;
  /* стиль сайта: чистый лист, как страница альбома */
}

.gallery-wrapper {
  max-width: 1100px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px 24px;
  opacity: 0;
  transform: translateY(40px);
  transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.gallery-visible {
  opacity: 1;
  transform: translateY(0);
}

/* левая и правая колонка */
.gallery-col {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* блоки с фото */
.photo-block {
  position: relative;
  width: 100%;
  background: #000;
  overflow: hidden;
  border-radius: 12px;
  /* никакой тени — в твоём сайте почти всё плоское, как печать на бумаге */
}

/* высокое фото */
.photo-block.tall {
  aspect-ratio: 3 / 4; /* вертикальное */
}

/* горизонтальные фото */
.photo-block.wide {
  aspect-ratio: 4 / 3; /* горизонтальное */
}

.photo-block img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

  /* чёрно-белый с чуть контраста, как ты просил */
  filter: grayscale(1) contrast(1.05);
  /* можно добавить лёгкое смягчение для мягкого вида */
  -webkit-filter: grayscale(1) contrast(1.05);
}

/* мобильный вид:
   на маленьких экранах не делаем две колонки.
   сначала высотное фото, потом два горизонтальных.
*/
@media (max-width: 768px) {
  .gallery-wrapper {
    grid-template-columns: 1fr;
    max-width: 500px;
  }

  .gallery-col-left,
  .gallery-col-right {
    gap: 24px;
  }
}
